import React, { useState } from 'react';
import CalendarAndButton from './CalendarAndButton';
import GamesTable from './GamesTable';
import Live from './Live';
import GameInfo from './GameInfo';
const moment = require('moment');

function GamesPerDateView () {
    const [date, setDate] = useState(new Date());
    const [error, setError] = useState(null);
    const [games, setGames] = useState([]);
    const [callToAPI, setCallToAPI] = useState(false);
    const [clickedButton, setClickedButton] = useState(false);
    let ts = Date.now();
    let date_ob = new Date(ts);
    let dateDay = date_ob.getDate();
    let monthDay = date_ob.getMonth() + 1;
    let yearDay = date_ob.getFullYear();
    if(dateDay < 10)
      dateDay = '0'+dateDay;
    
    if(monthDay < 10)
      monthDay = '0'+monthDay;
    let str = yearDay + '-' + monthDay + '-' + dateDay;
    var formattedDate = moment(date).format('YYYY-MM-DD');

    const [buttonGameClicked, setButtonGameClicked] = useState(false);
    const [game_Id, setGame_Id] = useState("");

    return (
        <React.Fragment>
            <CalendarAndButton
                date = {date}
                setDate = {setDate}
                error = {error}
                setError = {setError}
                games = {games}
                setGames = {setGames}
                callToAPI = {callToAPI}
                setCallToAPI = {setCallToAPI}
                setClickedButton = {setClickedButton}
                setButtonGameClicked = {setButtonGameClicked}
             />
             {clickedButton?
                buttonGameClicked?
                <GameInfo 
                    id = {game_Id}
                /> 
                :
                formattedDate === str?
                    <Live />
                    :
                    <GamesTable
                        date = {date}
                        setDate = {setDate}
                        error = {error}
                        setError = {setError}
                        games = {games}
                        setGames = {setGames}
                        callToAPI = {callToAPI}
                        setCallToAPI = {setCallToAPI}
                        setButtonGameClicked={setButtonGameClicked} 
                        setGame_Id={setGame_Id}
                    />
                :
                <React.Fragment></React.Fragment>
             }
        </React.Fragment>
    );
}

export default GamesPerDateView;