import React from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  Navigate
} from "react-router-dom";


import './App.css';

import GamesPerDateView from './components/GamesPerDateView';
import Live from './components/Live.js';
import StandingView from './components/standing-view';

import { createTheme, ThemeProvider } from "@mui/material/styles";

const theme = createTheme({
  typography: {
    fontFamily: [
      "system-ui",
      "-apple-system",
      "system-ui",
      "Helvetica Neue",
      "Helvetica",
      "Arial",
      "sans-serif"
    ].join(",")
  }
});

function App() {
  
  return (

    
    <ThemeProvider theme={theme}>
      <Router>
        <nav>
        <div className="navbar">
          <div className="container nav-container">
              <input className="checkbox" type="checkbox" name="" id="" />
              <div className="hamburger-lines">
                <span className="line line1"></span>
                <span className="line line2"></span>
                <span className="line line3"></span>
              </div>  
            <div className="logo">
              <h1>NBAScore</h1>
            </div>
            <div className="menu-items">
              <li>                
                <Link onClick={() => this.setState({})} to="/live">Live</Link>
              </li>
              <li>
                <Link onClick={() => this.setState({})} to="/standings">Standings</Link>
              </li>
              <li>
                <Link onClick={() => this.setState({})} to="/gamesPerDate">Games Per Date</Link>
              </li>
            </div>
          </div>
        </div>
       </nav>
        <Routes>
            <Route path="/" element={ <Navigate replace to="/live"/> } />
            <Route path="/live" element={ <Live /> } />        
            <Route path="/standings" element={ <StandingView/> } />        
            <Route path="/gamesPerDate" element={ <GamesPerDateView/> } />        
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App;