const GameInfoNavBar = (props) => {
    
    return (
        <div className="button-statistics">
            <button className="choose_statistic" onClick={() => props.setInfo("game_stats")}>
                Game Statistics
            </button>
            <button className="choose_statistic" onClick={() => props.setInfo("home")}>
                Home Players
            </button>
            <button className="choose_statistic" onClick={() => props.setInfo("visitors")}>
                Visitors Players
            </button>
            <button className="choose_statistic" onClick={() => props.setInfo("")}>
                Cancel View
            </button>
        </div>
    )
}

export default GameInfoNavBar;